@import url(https://fonts.googleapis.com/css?family=Comfortaa);
@import url(https://fonts.googleapis.com/css?family=Urbanist);
:root {


  
  --font-family-Comfortaa: 'Comfortaa', sans-serif;
  --font-family-Urbanist: 'Urbanist', sans-serif;

  --hg-gradient: linear-gradient(89.95deg, rgba(50, 177, 38, 0.6) 1.99%, rgba(36, 162, 233, 0.6) 85.34%, rgba(36, 162, 233, 0.6) 104.58%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: white;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #19177E;
  --color-subtext: #FF8A71;
}

* {
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

html {
  font-size: 100%;
}

a {
  color: unset;
  text-decoration: none;
}



.circle_top_login {
  position: absolute;
  width: 29.5rem;
  height: 29.5rem;
  border-radius: 100%;
  background: rgba(49, 177, 39, 0.63);
  z-index: -1;
  left:-8.628rem;
  top: -10.6875rem;

  -webkit-animation: roll-in-down 1500ms ease-out;

          animation: roll-in-down 1500ms ease-out;
}

.circle_bottom_signup {
  position: absolute;
  width: 29.5rem;
  height: 29.5rem;
  border-radius: 100%;
  background: #31B127A1;
  z-index: -1;
  left:-8.628rem;
  -webkit-animation: roll-in-top 1500ms ease-out;
          animation: roll-in-top 1500ms ease-out;
  bottom: -10.6875rem;
}

.circle_bottom_login {
  position: absolute;
  z-index: -1;
  right: -8.628rem;
  bottom: -10.6875rem;
  width:  29.5rem;
  height: 29.5rem;
  border-radius: 100%;
  background: #24A2E9;
  -webkit-animation: roll-in-top 1500ms ease-out;
          animation: roll-in-top 1500ms ease-out;
}

.circle_top_signup {
  z-index: -1;
  position: absolute;
  width: 29.5rem;
  height: 29.5rem;

  right:-8.628rem;
  top: -10.6875rem;
  border-radius: 100%;
  background: #24A2E9;
  -webkit-animation: roll-in-down 1500ms ease-out;
          animation: roll-in-down 1500ms ease-out;
}

@-webkit-keyframes roll-in-top {

  0% {
    -webkit-transform: translateY(-29.5rem) rotate(-540deg);
            transform: translateY(-29.5rem) rotate(-540deg);
    opacity: 1;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@keyframes roll-in-top {

  0% {
    -webkit-transform: translateY(-29.5rem) rotate(-540deg);
            transform: translateY(-29.5rem) rotate(-540deg);
    opacity: 1;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@-webkit-keyframes roll-in-down {

  0% {
    -webkit-transform: translateY(29.5rem) rotate(540deg);
            transform: translateY(29.5rem) rotate(540deg);
    opacity: 1;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

@keyframes roll-in-down {

  0% {
    -webkit-transform: translateY(29.5rem) rotate(540deg);
            transform: translateY(29.5rem) rotate(540deg);
    opacity: 1;
  }

  100% {

    -webkit-transform: translateY(0px) rotate(0deg);

            transform: translateY(0px) rotate(0deg);
    opacity: 1;
  }
}

.login_signup_container {
  z-index: -1;
  width: 100vw;
  height: 100vh;

  position: absolute;
  overflow-y: hidden;
  overflow-x: hidden;
}

.login_signup_glass {
  position: absolute;
  left: 2.5625rem;
  top: 1.875rem;
  bottom: 1.875rem;
  right: 2.5625rem;
  background: rgba(236, 236, 236, 0.34);
  -webkit-backdrop-filter: blur(.625rem);
          backdrop-filter: blur(.625rem);
  border-radius: 1.875rem;
 

  border: .1rem solid;
  border-top-color: #EDEDED;
  border-right-color: #EDEDED;
  border-left-color: #E0E0E0;
  border-bottom-color: #E0E0E0;
}

.login_line {
  z-index: -1;
  position: absolute;
  top: 28vmin;
  -webkit-animation: login_line 1500ms ease-out;
          animation: login_line 1500ms ease-out;
}

.signup_line {
  position: absolute;
  top: 37vmin;
  left: 175vmin;
  -webkit-animation: signup_line 1500ms ease-out;
          animation: signup_line 1500ms ease-out;
}

@-webkit-keyframes signup_line {

  0% {
    -webkit-transform: translateX(100px) rotate(200deg);
            transform: translateX(100px) rotate(200deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: rotate(0deg);

            transform: rotate(0deg);
    opacity: 1;
  }
}

@keyframes signup_line {

  0% {
    -webkit-transform: translateX(100px) rotate(200deg);
            transform: translateX(100px) rotate(200deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: rotate(0deg);

            transform: rotate(0deg);
    opacity: 1;
  }
}

@-webkit-keyframes login_line {

  0% {
    -webkit-transform: translateX(100px) rotate(200deg);
            transform: translateX(100px) rotate(200deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: rotate(0deg);

            transform: rotate(0deg);
    opacity: 1;
  }
}

@keyframes login_line {

  0% {
    -webkit-transform: translateX(100px) rotate(200deg);
            transform: translateX(100px) rotate(200deg);
    opacity: 0;
  }

  100% {

    -webkit-transform: rotate(0deg);

            transform: rotate(0deg);
    opacity: 1;
  }
}

.Home_curve_line {
  position: absolute;
  top: 13.5vmin;
}



@media(max-width:998px) {
  html {
    font-size: 55%;
  }
}
@media(max-width:768px) {
  html {
    font-size: 45%;
  }
}
